
  <template>
    <tr>
      <td v-show="!is_edit">{{ lab.labName }}</td>
      <td v-show="!is_edit">{{ lab.labDescription }}</td>
      <td v-show="!is_edit">{{ lab.directorid }}</td>
      <td v-show="!is_edit">{{ lab.secretaryname }}</td>
      <td v-show="!is_edit">
        <!---->
        <el-button type="success" @click="modify"></el-button>
        <el-button type="danger" @click="deleteForm"></el-button>
      </td>
  
      <td v-show="is_edit">
        {{ lab.labName }}
      </td>

      <td v-show="is_edit">
        <input type="text" class="w-50" v-model="lab.labDescription" />
      </td>
      <td v-show="is_edit">
        <!-- <input type="text" class="w-50" v-model="lab.directorid" /> -->
        <el-select v-model="lab.directorid" placeholder="" @focus="selectDirector">
                        <el-option
                          v-for="item in littleoptions"
                          :key="item"
                          :label="item"
                          :value="item">
                        </el-option>
                      </el-select>
      </td>
      <td v-show="is_edit">
        <!-- <input type="text" class="w-50" v-model="lab.secretaryname" /> -->
        <el-select v-model="lab.secretaryname" placeholder="" @focus="selectSecretary">
                        <el-option
                          v-for="item in littleoptions2"
                          :key="item"
                          :label="item"
                          :value="item">
                        </el-option>
                      </el-select>
      </td>

      <td v-show="is_edit">
        <!---->
        <el-button type="success" @click="save"></el-button>
        <el-button type="danger" @click="deleteForm"></el-button>
      </td>
    </tr>
  </template>
  
  <script>
  import axios from "axios";
  import { EventBus } from "./EventBus.js";
  export default {
      props:["lab"],
      data(){
          return{
              is_edit:false,
              littleoptions: [],
              littleoptions2:[],
          }
      },
      methods:{
          modify(){
              this.is_edit = true;
          },
          save(){
              axios({
                  method:"post",
                  url:"http://localhost:8080/updateResearchLab",
                  data:this.lab
              }).then(res=>{
                //
                  console.log(res);
                  this.is_edit = false;
                  axios({
                      url: "http://localhost:8080/findResearchLab",
                      method: "GET",
                    }).then((res) => {
                      console.log(res.data);
                      EventBus.$emit("lab-updated", res.data);
                    });

              }).catch(err=>{
                  console.log(err);
                  this.$message.error(err);
              
              })
          },
          deleteForm(){
              axios({
                  method:"post",
                  url:"http://localhost:8080/deleteResearchLab",
                  data:this.lab
              }).then(res=>{
                  console.log(res);
                  //
                  this.is_edit = false;
                  axios({
                        url: "http://localhost:8080/findResearchLab",
                        method: "GET",
                      }).then((res) => {
                        console.log(res.data);
                        EventBus.$emit("lab-updated", res.data);
                      });
                  
              }).catch(err=>{
                  console.log(err);
                  this.$message.error(err);
              
              })
          },
          selectDirector(){
            console.log("selectDirector")
            axios({
          url: "http://localhost:8080/findDirectorName",
          method: "GET",
        })
          .then(response => {
            const data = response.data;
            console.log(data);
    
            this.littleoptions=data;
            console.log(this.options)
    
          })
          .catch(error => {
            console.error(error);
          });
        },
        selectSecretary(){
            axios({
          url: "http://localhost:8080/findSecretaryName",
          method: "GET",
        })
          .then(response => {
            const data = response.data;
            console.log(data);
    
            this.littleoptions2=data;
            console.log(this.options2)
    
          })
          .catch(error => {
            console.error(error);
          });
        },
     
    }
  };
  </script>
  
  <style>
  </style>
  
  
