
<template>
    <tr>
      <td v-show="!is_edit">{{ partner.partnerId }}</td>
      <td v-show="!is_edit">{{ partner.projectId }}</td>
      <td v-show="!is_edit">{{ partner.partnerName }}</td>
      <td v-show="!is_edit">{{ partner.address }}</td>
      <td v-show="!is_edit">{{ partner.responsiblepeople }}</td>
      <td v-show="!is_edit">{{ partner.responsiblePhone }}</td>
      <td v-show="!is_edit">{{ partner.contactPerson }}</td>
      <td v-show="!is_edit">{{ partner.contactPhone }}</td>
      <td v-show="!is_edit">
        <!---->
        <el-button type="success" @click="modify"></el-button>
        <el-button type="danger" @click="deleteForm"></el-button>
      </td>
  
      <td v-show="is_edit">
        {{  partner.partnerId }}
      </td>


      <td v-show="is_edit">
        <!-- <input type="text" class="w-50" v-model="partner.projectId" /> -->
        <el-select v-model="partner.projectId" placeholder="ID" @focus="selectResearchProject">
                        <el-option
                          v-for="item in littleoptions"
                          :key="item"
                          :label="item"
                          :value="item">
                        </el-option>
                      </el-select>
      </td>
      <td v-show="is_edit">
        <input type="text" class="w-50" v-model="partner.partnerName" />
      </td>
    
      <td v-show="is_edit">
        <input type="text" class="w-50" v-model="partner.address" />
      </td>
      <td v-show="is_edit">
        <input type="text" class="w-50" v-model="partner.responsiblepeople" />
      </td>
      <td v-show="is_edit">
        <input type="text" class="w-50" v-model="partner.responsiblePhone" />
      </td>
      <td v-show="is_edit">
        <input type="text" class="w-50" v-model="partner.contactPerson" />
      </td>
      <td v-show="is_edit">
        <input type="text" class="w-50" v-model="partner.contactPhone" />
      </td>
      <td v-show="is_edit">
        <!---->
        <el-button type="success" @click="save"></el-button>
        <el-button type="danger" @click="deleteForm"></el-button>
      </td>
    </tr>
  </template>
  
  <script>
  import axios from "axios";
  import { EventBus } from "./EventBus.js";
  export default {
    name: "Partner",
      props:["partner"],
      data(){
          return{
              is_edit:false,
              littleoptions: [],
          }
      },
      methods:{
          modify(){
              this.is_edit = true;
          },
          save(){
              axios({
                  method:"post",
                  url:"http://localhost:8080/updatePartner",
                  data:this.partner
              }).then(res=>{
                //
                  console.log(res);
                  this.is_edit = false;
                  axios({
                      url: "http://localhost:8080/findPartner",
                      method: "GET",
                    }).then((res) => {
                      console.log(res.data);
                      EventBus.$emit("partner-updated", res.data);
                    });

              }).catch(err=>{
                  console.log(err);
                  this.$message.error(err);
              
              })
          },
          deleteForm(){
              axios({
                  method:"post",
                  url:"http://localhost:8080/deletePartner",
                  data:this.partner
              }).then(res=>{
                  console.log(res);
                  //
                  this.is_edit = false;
                  axios({
                        url: "http://localhost:8080/findPartner",
                        method: "GET",
                      }).then((res) => {
                        console.log(res.data);
                        EventBus.$emit("partner-updated", res.data);
                      });
                  
              }).catch(err=>{
                  console.log(err);
                  this.$message.error(err);
              
              })
          },
          
          selectResearchProject(){
            axios({
          url: "http://localhost:8080/findResearchProjectName",
          method: "GET",
        })
          .then(response => {
            const data = response.data;
            console.log(data);
    
            this.littleoptions=data;
            console.log(this.options)
    
          })
          .catch(error => {
            console.error(error);
          });
          }
     
    }
  };
  </script>
  
  <style>
  </style>
  
  
  <!-- :Vue.jsv-bindVueHTML:style="{ backgroundColor: selectedColor } :src="imageUrl"
  v-modelVue.jsVue
  {{}}Vue.jsVueVue
  -->