
<template>
    <tr>
      <td v-show="!is_edit">{{ monitor.monitorName }}</td>
      <td v-show="!is_edit">{{ monitor.address }}</td>
      <td v-show="!is_edit">{{ monitor.responsiblePerson }}</td>
      <td v-show="!is_edit">{{ monitor.responsiblePhone }}</td>
      <td v-show="!is_edit">{{ monitor.contactPerson }}</td>
      <td v-show="!is_edit">{{ monitor.contactPhone }}</td>
      <td v-show="!is_edit">
        <!---->
        <el-button type="success" @click="modify"></el-button>
        <el-button type="danger" @click="deleteForm"></el-button>
      </td>
  
      <td v-show="is_edit">
        {{  monitor.monitorName }}
      </td>
      <td v-show="is_edit">
        <input type="text" class="w-50" v-model="monitor.address" />
      </td>
      <td v-show="is_edit">
        <input type="text" class="w-50" v-model="monitor.responsiblePerson" />
      </td>
    
      <td v-show="is_edit">
        <input type="text" class="w-50" v-model="monitor.responsiblePhone" />
      </td>
      <td v-show="is_edit">
        <input type="text" class="w-50" v-model="monitor.contactPerson" />
      </td>
      <td v-show="is_edit">
        <input type="text" class="w-50" v-model="monitor.contactPhone" />
      </td>
      <td v-show="is_edit">
        <!---->
        <el-button type="success" @click="save"></el-button>
        <el-button type="danger" @click="deleteForm"></el-button>
      </td>
    </tr>
  </template>
  
  <script>
  import axios from "axios";
  import { EventBus } from "./EventBus.js";
  export default {
    name: "Monitor",
      props:["monitor"],
      data(){
          return{
              is_edit:false,
          }
      },
      methods:{
          modify(){
              this.is_edit = true;
          },
          save(){
              axios({
                  method:"post",
                  url:"http://localhost:8080/updateQualityMonitor",
                  data:this.monitor
              }).then(res=>{
                //
                  console.log(res);
                  this.is_edit = false;
                  axios({
                      url: "http://localhost:8080/findQualityMonitor",
                      method: "GET",
                    }).then((res) => {
                      console.log(res.data);
                      EventBus.$emit("monitor-updated", res.data);
                    });

              }).catch(err=>{
                  console.log(err);
                  this.$message.error(err);
              
              })
          },
          deleteForm(){
              axios({
                  method:"post",
                  url:"http://localhost:8080/deleteQualityMonitor",
                  data:this.monitor
              }).then(res=>{
                  console.log(res);
                  //
                  this.is_edit = false;
                  axios({
                        url: "http://localhost:8080/findQualityMonitor",
                        method: "GET",
                      }).then((res) => {
                        console.log(res.data);
                        EventBus.$emit("monitor-updated", res.data);
                      });
                  
              }).catch(err=>{
                  console.log(err);
                  this.$message.error(err);
              
              })
          },
          
        
     
    }
  };
  </script>
  
  <style>
  </style>
  
  
