<template>
    <tr>
      <td v-show="!is_edit">{{ secretary.secretaryNo }}</td>
      <td v-show="!is_edit">{{ secretary.name }}</td>
      <td v-show="!is_edit">{{ secretary.gender }}</td>
      <td v-show="!is_edit">{{ secretary.age }}</td>
      <td v-show="!is_edit">{{ secretary.hireDate }}</td>
      <td v-show="!is_edit">{{ secretary.duty }}</td>
      <td v-show="!is_edit">
        <!---->
        <el-button type="success" @click="modify"></el-button>
        <el-button type="danger" @click="deleteStudent"></el-button>
      </td>
  
      <td v-show="is_edit">
        {{ secretary.secretaryNo }}
      </td>
      <td v-show="is_edit">
        <input type="text" class="w-50" v-model="secretary.name" />
      </td>
      <td v-show="is_edit">
        <!-- <input type="text" class="w-50" v-model="secretary.gender" /> -->
        <el-radio v-model="secretary.gender" label=""></el-radio>
                <el-radio v-model="secretary.gender" label=""></el-radio>
      </td>
      <td v-show="is_edit">
        <input type="text" class="w-50" v-model.number="secretary.age" />
      </td>
      <td v-show="is_edit">
        <!-- <input type="text" class="w-50" v-model="secretary.hireDate" /> -->
        <el-date-picker
      v-model="secretary.hireDate"
      type="date"
      placeholder="">
    </el-date-picker>
      </td>
      <td v-show="is_edit">
        <input type="text" class="w-50" v-model="secretary.duty" />
      </td>
      <td v-show="is_edit">
        <!---->
        <el-button type="success" @click="save"></el-button>
        <el-button type="danger" @click="deleteStudent"></el-button>
      </td>
    </tr>
  </template>
  
  <script>
  import axios from "axios";
  import { EventBus } from "./EventBus.js";
  export default {
      props:["secretary"],
      data(){
          return{
              is_edit:false,
          }
      },
      methods:{
          modify(){
              this.is_edit = true;
          },
          save(){
            const originalDate = new Date(this.secretary.hireDate);
            this.secretary.hireDate=originalDate;
              axios({
                  method:"post",
                  url:"http://localhost:8080/updateSecretary",
                  data:this.secretary
              }).then(res=>{
                  console.log(res);
                  this.is_edit = false;
                  axios({
          url: "http://localhost:8080/findSecretary",
          method: "GET",
        }).then((res) => {
          console.log(res.data);
          EventBus.$emit("secretary-updated", res.data);
        });
              }).catch(err=>{
                  console.log(err);
                  this.$message.error(err);
              
              })
              
          },
          deleteStudent(){
            const originalDate = new Date(this.secretary.hireDate);
            this.secretary.hireDate=originalDate;
              axios({
                  method:"post",
                  url:"http://localhost:8080/deleteSecretary",
                  data:this.secretary
              }).then(res=>{
                  console.log(res);
                  this.is_edit = false;
                  axios({
          url: "http://localhost:8080/findSecretary",
          method: "GET",
        }).then((res) => {
          console.log(res.data);
          EventBus.$emit("secretary-updated", res.data);
        });
                  
              }).catch(err=>{
                  console.log(err);
                  this.$message.error(err);
              
              })
             
          }
      }
  };
  </script>
  
  <style>
  </style>
  
  
  <!-- :Vue.jsv-bindVueHTML:style="{ backgroundColor: selectedColor } :src="imageUrl"
  v-modelVue.jsVue
  {{}}Vue.jsVueVue
  -->