<template>
    <tr>
      <td v-show="!is_edit">{{ director.directorNo }}</td>
      <td v-show="!is_edit">{{ director.startDate }}</td>
      <td v-show="!is_edit">{{ director.term }}</td>
      <td v-show="!is_edit">{{ director.researchername }}</td>
      <td v-show="!is_edit">
        <!---->
        <el-button type="success" @click="modify"></el-button>
        <el-button type="danger" @click="deleteForm"></el-button>
      </td>
  
      <td v-show="is_edit">
        {{ director.directorNo }}
      </td>

      <td v-show="is_edit">
        <el-date-picker
      v-model="director.startDate"
      type="date"
      placeholder="">
    </el-date-picker>
      </td>
      <td v-show="is_edit">
        <input type="text" class="w-50" v-model.number="director.term" />
      </td>
      <td v-show="is_edit">
        <!-- <input type="text" class="w-50" v-model="director.researchername" /> -->
        <el-select v-model="director.researchername" placeholder="" @focus="selectresearcher">
                        <el-option
                          v-for="item in littleoptions"
                          :key="item"
                          :label="item"
                          :value="item">
                        </el-option>
                      </el-select>
      </td>

      <td v-show="is_edit">
        <!---->
        <el-button type="success" @click="save"></el-button>
        <el-button type="danger" @click="deleteForm"></el-button>
      </td>
    </tr>
  </template>
  
  <script>
  import axios from "axios";
  import { EventBus } from "./EventBus.js";
  export default {
      props:["director"],
      data(){
          return{
              is_edit:false,
              littleoptions:[],
          }
      },
      methods:{
          modify(){
              this.is_edit = true;
          },
          save(){
            const originalDate = new Date(this.director.startDate);
            this.director.startDate=originalDate;
              axios({
                  method:"post",
                  url:"http://localhost:8080/updateDirector",
                  data:this.director
              }).then(res=>{
                  console.log(res);
                  this.is_edit = false;
                  axios({
          url: "http://localhost:8080/findDirector",
          method: "GET",
        }).then((res) => {
          console.log(res.data);
          EventBus.$emit("director-updated", res.data);
        });
              }).catch(err=>{
                  console.log(err);
                  this.$message.error(err);
              
              })
              
          },
          deleteForm(){
            console.log(this.director)
            const originalDate = new Date(this.director.startDate);
            this.director.startDate=originalDate;
              axios({
                  method:"post",
                  url:"http://localhost:8080/deleteDirector",
                  data:this.director
              }).then(res=>{
                  console.log(res);
                  this.is_edit = false;
                  axios({
                    url: "http://localhost:8080/findDirector",
                    method: "GET",
                  }).then((res) => {
                    console.log(res.data);
                    EventBus.$emit("director-updated", res.data);
                  });    
                 }).catch(err=>{
                     console.log(err);
                     this.$message.error(err);
                 
                 })
          },
          selectresearcher(){
            console.log("selectDirector")
            axios({
          url: "http://localhost:8080/findResearcherName",
          method: "GET",
        })
          .then(response => {
            const data = response.data;
            console.log(data);
    
            this.littleoptions=data;
            console.log(this.options)
    
          })
          .catch(error => {
            console.error(error);
          });
        },
      }
  };
  </script>
  
  <style>
  </style>
  
  
