
<template>
    <tr>
      <td v-show="!is_edit">{{ researcher.researcherNo }}</td>
      <td v-show="!is_edit">{{ researcher.labName }}</td>
      <td v-show="!is_edit">{{ researcher.name }}</td>
      <td v-show="!is_edit">{{ researcher.gender }}</td>
      <td v-show="!is_edit">{{ researcher.title }}</td>
      <td v-show="!is_edit">{{ researcher.age }}</td>
      <td v-show="!is_edit">{{ researcher.researchDirection }}</td>
      <td v-show="!is_edit">
        <!---->
        <el-button type="success" @click="modify"></el-button>
        <el-button type="danger" @click="deleteForm"></el-button>
      </td>
  
      <td v-show="is_edit">
        {{ researcher.researcherNo }}
      </td>


      <td v-show="is_edit">
        <el-select v-model="researcher.labName" placeholder="" @focus="selectresearcher">
                        <el-option
                          v-for="item in littleoptions"
                          :key="item"
                          :label="item"
                          :value="item">
                        </el-option>
                      </el-select>
      </td>
      <td v-show="is_edit">
        <input type="text" class="w-50" v-model="researcher.name" />
      </td>
      <td v-show="is_edit">
        <!-- <input type="text" class="w-50" v-model="secretary.gender" /> -->
        <el-radio v-model="researcher.gender" label=""></el-radio>
                <el-radio v-model="researcher.gender" label=""></el-radio>
      </td>
      <td v-show="is_edit">
        <input type="text" class="w-50" v-model="researcher.title" />
      </td>
      <td v-show="is_edit">
        <input type="text" class="w-50" v-model="researcher.age" />
      </td>
      <td v-show="is_edit">
        <input type="text" class="w-50" v-model="researcher.researchDirection" />
      </td>
      <td v-show="is_edit">
        <!---->
        <el-button type="success" @click="save"></el-button>
        <el-button type="danger" @click="deleteForm"></el-button>
      </td>
    </tr>
  </template>
  
  <script>
  import axios from "axios";
  import { EventBus } from "./EventBus.js";
  export default {
    name: "Researcher",
      props:["researcher"],
      data(){
          return{
              is_edit:false,
              littleoptions: [],
          }
      },
      methods:{
          modify(){
              this.is_edit = true;
          },
          save(){
              axios({
                  method:"post",
                  url:"http://localhost:8080/updateResearcher",
                  data:this.researcher
              }).then(res=>{
                //
                  console.log(res);
                  this.is_edit = false;
                  axios({
                      url: "http://localhost:8080/findResearcher",
                      method: "GET",
                    }).then((res) => {
                      console.log(res.data);
                      EventBus.$emit("researcher-updated", res.data);
                    });

              }).catch(err=>{
                  console.log(err);
                  this.$message.error(err);
              
              })
          },
          deleteForm(){
              axios({
                  method:"post",
                  url:"http://localhost:8080/deleteResearcher",
                  data:this.researcher
              }).then(res=>{
                  console.log(res);
                  //
                  this.is_edit = false;
                  axios({
                        url: "http://localhost:8080/findResearcher",
                        method: "GET",
                      }).then((res) => {
                        console.log(res.data);
                        EventBus.$emit("researcher-updated", res.data);
                      });
                  
              }).catch(err=>{
                  console.log(err);
                  this.$message.error(err);
              
              })
          },
          selectresearcher(){
            console.log("selectDirector")
            axios({
          url: "http://localhost:8080/findResearchLabName",
          method: "GET",
        })
          .then(response => {
            const data = response.data;
            console.log(data);
    
            this.littleoptions=data;
            console.log(this.options)
    
          })
          .catch(error => {
            console.error(error);
          });
        },
        
     
    }
  };
  </script>
  
  <style>
  </style>
  
  
