
<template>
    <tr>
      <td v-show="!is_edit">{{ project.projectNo }}</td>
      <td v-show="!is_edit">{{ project.projectLeader }}</td>
      <td v-show="!is_edit">{{ project.projectName }}</td>
      <td v-show="!is_edit">{{ project.researchContent }}</td>

      <td v-show="!is_edit">{{ project.totalFunding }}</td>
      <td v-show="!is_edit">{{ project.startDate }}</td>
      <td v-show="!is_edit">{{ project.completionDate }}</td>
      <td v-show="!is_edit">{{ project.clientName }}</td>
      <td v-show="!is_edit">{{ project.monitorName }}</td>
      <td v-show="!is_edit">
        <!---->
        <el-button type="success" @click="modify"></el-button>
        <el-button type="danger" @click="deleteForm"></el-button>
      </td>
  
      <td v-show="is_edit">
        {{ project.projectNo }}
      </td>

      <td v-show="is_edit">
        <el-select v-model="project.projectLeader" placeholder="" @focus="selectDirector">
                        <el-option
                          v-for="item in littleoptions"
                          :key="item"
                          :label="item"
                          :value="item">
                        </el-option>
                      </el-select>
      </td>
      <td v-show="is_edit">
        <input type="text" class="w-50" v-model="project.projectName" />
      </td>
      <td v-show="is_edit">
        <input type="text" class="w-50" v-model="project.researchContent" />
      </td>
      <td v-show="is_edit">
        <input type="text" class="w-50" v-model.number="project.totalFunding" />
      </td>
      <td v-show="is_edit">
        <el-date-picker v-model="project.startDate" type="date" placeholder=""></el-date-picker>
      </td>
      <td v-show="is_edit">
        <el-date-picker v-model="project.completionDate" type="date" placeholder=""> </el-date-picker>
      </td>
      <td v-show="is_edit">
        <el-select v-model="project.clientName" placeholder="" @focus="selectClient">
                        <el-option
                          v-for="item in littleoptions2"
                          :key="item"
                          :label="item"
                          :value="item">
                        </el-option>
                      </el-select>
      </td>
      <td v-show="is_edit">
        <el-select v-model="project.monitorName" placeholder="" @focus="selectMonitor">
                        <el-option
                          v-for="item in littleoptions3"
                          :key="item"
                          :label="item"
                          :value="item">
                        </el-option>
                      </el-select>
      </td>

      <td v-show="is_edit">
        <!---->
        <el-button type="success" @click="save"></el-button>
        <el-button type="danger" @click="deleteForm"></el-button>
      </td>
    </tr>
  </template>
  
  <script>
  import axios from "axios";
  import { EventBus } from "./EventBus.js";
  export default {
    name: "Project",
      props:["project"],
      data(){
          return{
              is_edit:false,
              littleoptions: [],
              littleoptions2:[],
              littleoptions3:[],
          }
      },
      methods:{
          modify(){
              this.is_edit = true;
          },
          save(){
            const originalDate = new Date(this.project.startDate);
            this.project.startDate=originalDate;
            const originalDate2 = new Date(this.project.completionDate);
            this.project.completionDate=originalDate2;
              axios({
                  method:"post",
                  url:"http://localhost:8080/updateResearchProject",
                  data:this.project
              }).then(res=>{
                //
                  console.log(res);
                  this.is_edit = false;
                  axios({
                      url: "http://localhost:8080/findResearchProject",
                      method: "GET",
                    }).then((res) => {
                      console.log(res.data);
                      EventBus.$emit("project-updated", res.data);
                    });

              }).catch(err=>{
                  console.log(err);
                  this.$message.error(err);
              
              })
          },
          deleteForm(){
              axios({
                  method:"post",
                  url:"http://localhost:8080/deleteResearchProject",
                  data:this.project
              }).then(res=>{
                  console.log(res);
                  //
                  this.is_edit = false;
                  axios({
                        url: "http://localhost:8080/findResearchProject",
                        method: "GET",
                      }).then((res) => {
                        console.log(res.data);
                        EventBus.$emit("project-updated", res.data);
                      });
                  
              }).catch(err=>{
                  console.log(err);
                  this.$message.error(err);
              
              })
          },


          selectDirector(){
            console.log("selectDirector")
            axios({
          url: "http://localhost:8080/findResearcherName",
          method: "GET",
        })
          .then(response => {
            const data = response.data;
            console.log(data);
    
            this.littleoptions=data;
            console.log(this.options)
    
          })
          .catch(error => {
            console.error(error);
          });
        },
        selectClient(){
            axios({
          url: "http://localhost:8080/findClientName",
          method: "GET",
        })
          .then(response => {
            const data = response.data;
            console.log(data);
    
            this.littleoptions2=data;
            console.log(this.options2)
    
          })
          .catch(error => {
            console.error(error);
          });
        },

        selectMonitor(){
            axios({
          url: "http://localhost:8080/findQualityMonitorName",
          method: "GET",
        })
          .then(response => {
            const data = response.data;
            console.log(data);
    
            this.littleoptions3=data;
            console.log(this.options3)
    
          })
          .catch(error => {
            console.error(error);
          });
        },
     
    }
  };
  </script>
  
  <style>
  </style>
  
  
  <!-- :Vue.jsv-bindVueHTML:style="{ backgroundColor: selectedColor } :src="imageUrl"
  v-modelVue.jsVue
  {{}}Vue.jsVueVue
  -->